:root {
  --items: 1;
  --indent: 30px;
}

* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  text-align: center;
  background-color: #fafafa;
  margin: 0;
  padding-top: 60px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #767676;
  border-radius: 2px;
  outline: none;
  padding: 5px 25px;
  box-shadow: 0 4px 4px #00000026;
}

.search-form {
  width: 100%;
  height: 50px;
  background-color: #96fbff;
  justify-content: center;
  gap: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.search-form input {
  padding: 5px;
}

.gallery {
  gap: var(--indent);
  max-width: 480px;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
}

.photo-card {
  flex-basis: calc((100% - var(--indent) * (var(--items)  - 1)) / (var(--items)));
  display: grid;
}

.info {
  justify-content: space-around;
  margin-top: auto;
  padding: 10px;
  font-size: 10px;
  display: flex;
}

.info-item {
  text-align: center;
  margin: 0;
}

b {
  display: block;
}

.load-more {
  display: inline-flex;
}

.is-hidden {
  display: none;
}

@media screen and (min-width: 768px) {
  .gallery {
    max-width: 768px;
  }

  .photo-card {
    --items: 2;
  }

  .info {
    font-size: 14px;
  }
}

@media screen and (min-width: 1200px) {
  .gallery {
    max-width: 1200px;
  }

  .photo-card {
    --items: 3;
  }
}

